import React, { useState, useContext } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'

import ContextMenu from './PhotoSeries/views/controls/ContextMenu'
import Toolbar from '@material-ui/core/Toolbar'
import AppBar from '@material-ui/core/AppBar'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
import AppLogoSimple from '../icons/AppLogoSimple'
import { AppContext } from '../contexts'

import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'

// import MenuIcon from '@material-ui/core/Menu'
import { makeStyles } from '@material-ui/core/styles'
import { ThemeProvider } from '@material-ui/styles'
import themes from '../themes'

const style = theme => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    padding: theme.spacing(0, 1),
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    background: `linear-gradient(90deg, ${theme.palette.primary.dark} 0%, ${theme.palette.secondary.dark} 100%)`
  },
  searchIcon: {
    opacity: 0.3,
    marginRight: theme.spacing(1)
  },
  transparent: {
    backgroundColor: 'transparent',
    background: 'none',
    color: theme.palette.text.primary
    // borderBottom: `2px transparent`
  },
  transparentIcon: {
    color: 'white',
    '& path': {
      stroke: '#00000055',
      strokeWidth: 0.5
    }
  },
  children: {
    flexGrow: 1,
    padding: theme.spacing(0, 1)
  },
  iconButton: {
    padding: theme.spacing(1, 0)
  },
  logo: {
    height: 40,
    width: 40
  }
})
const useStyles = makeStyles(style)

const TopBar = ({ className, children, isTransparent, menuItems, hideLogo }) => {
  const classes = useStyles()
  const [contextEl, setContextEl] = useState()
  const { signOut, signedIn, forceSignIn } = useContext(AppContext)

  let finalMenuItems = []
  if (menuItems) {
    finalMenuItems = [ ...menuItems, { divider: true } ]
  }

  return (
    <ThemeProvider theme={themes.topbar}>
      <AppBar position='static'>
        <Toolbar className={classNames(classes.root, className, isTransparent ? classes.transparent : null)} disableGutters>
          {!hideLogo && (
            <IconButton className={classes.iconButton} color='inherit'>
              <AppLogoSimple className={classNames(classes.logo, isTransparent ? classes.transparentIcon : null)} />
            </IconButton>
          )}
          <div className={classes.children}>
            {children}
          </div>
          {signedIn && (
            <IconButton
              color='inherit'
              className={classes.iconButton}
              aria-label='Menu'
              onClick={event => setContextEl(event.currentTarget)}
              // onClick={() => setDrawerOpen(!drawerOpen)}
            >
              <MenuIcon color='inherit' />
            </IconButton>
          )}
          {!signedIn && (
            <IconButton
              color='primary'
              className={classes.iconButton}
              aria-label='Menu'
              onClick={forceSignIn}
            >
              <AccountCircleIcon color='primary' />
            </IconButton>
          )}

          <ContextMenu
            anchorEl={contextEl}
            onClose={() => setContextEl(null)}
            menuItems={[
              ...finalMenuItems,
              {
                text: 'Sign Out',
                icon: ExitToAppIcon,
                action: signOut
              }
            ]}
          />
        </Toolbar>
      </AppBar>
    </ThemeProvider>
  )
}

TopBar.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  isTransparent: PropTypes.bool,
  hideLogo: PropTypes.bool,
  menuItems: PropTypes.arrayOf(PropTypes.shape({
    callback: PropTypes.func,
    text: PropTypes.string,
    icon: PropTypes.any,
    color: PropTypes.string
  }))
}

export default TopBar
